<template>
  <div id="detailedMV">
    <div class="main">
      <!--====================  左边   =====================-->
      <div class="left">
        <!-- mv -->
        <div class="mv">
          <video
            :src="videoUrl"
            autoplay
            controls
          ></video>
        </div>
        <!-- text -->
        <div class="text">
          <div class="author">
            <div class="textImg">
              <img :src="this.mvDetailList.cover" alt="" />
            </div>
            <div class="authorName">
              <span>{{this.mvDetailList.artistName}}</span>
            </div>
          </div>
          <div class="name">
            <span>{{this.mvDetailList.name}}</span>
          </div>
          <div class="quantity">
            <span>发布：</span>
            <span>{{this.mvDetailList.publishTime}}</span>
            <span class="playNum">播放：</span>
            <span>{{this.mvDetailList.playCount}}次</span>
          </div>
          <div class="type">
            <span>01</span>
            <span>02</span>
            <span>03</span>
            <span>04</span>
          </div>
        </div>
      </div>
      <!-- ==================== 右边 ===================-->
      <div class="right">
        <ul>
          <li class="items" v-for="(item, index) in mvSimiList" :key="index">
            <div class="chart" @click="playmvs(item.id)">
              <div class="amount">
                <span class="icon">▶</span><span>{{item.playCount}}</span>
              </div>
              <div class="chartImg">
                <img :src="item.cover" alt="" />
              </div>
              <div class="duration">
                <span class="time">{{item.duration}}</span>
              </div>
            </div>
            <div class="word">
              <div class="name">
                <span
                  >{{item.name}}</span
                >
              </div>
              <div class="author">
                <span>{{item.artistName}}</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="zhanwei"></div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      mvids: this.$route.query.mvid,
      // 视频url
      videoUrl: '',
      // 相似mv
      mvSimiList: [],
      // 获取mv数据
      mvDetailList: {}
    }
  },
  created () {
    // 获取mvurl
    this.mvUrl()
    // 获取mv数据
    this.mvSimi()
    this.mvDetail()
  },
  methods: {
    // 获取mvurl
    mvUrl () {
      axios({
        url: 'https://autumnfish.cn/mv/url',
        method: 'get',
        params: {
          id: this.mvids
        }
      }).then(res => {
        // console.log(res)
        if (res.data.code === 200) {
          this.videoUrl = res.data.data.url
        }
      })
    },
    // 获取mv数据
    mvSimi () {
      axios({
        url: 'https://autumnfish.cn/simi/mv',
        method: 'get',
        params: {
          mvid: this.mvids
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.mvSimiList = res.data.mvs
          // 处理播放次数
          for (var i = 0; i < this.mvSimiList.length; i++) {
            if (this.mvSimiList[i].playCount > 100000) {
              this.mvSimiList[i].playCount = parseInt(this.mvSimiList[i].playCount / 10000) + '万'
            }
          }
          //   处理时长
          for (let i = 0; i < this.mvSimiList.length; i++) {
            //   获取总毫秒
            var duration = this.mvSimiList[i].duration
            //   总毫秒数X= / 1000 取整
            //   获得分数 min X / 60
            //   获得秒数 sec X % 60
            var min = parseInt(duration / 1000 / 60)
            var sec = parseInt((duration / 1000) % 60)
            if (min < 10) {
              min = '0' + min
            }
            if (sec < 10) {
              sec = '0' + sec
            }
            // console.log(min + ':' + sec)
            this.mvSimiList[i].duration = `${min}:${sec}`
          }
        }
      })
    },
    playmvs (ID) {
      // console.log(ID)
      this.mvids = ID
      // 获取mvurl
      this.mvUrl()
      // 获取mv数据
      this.mvSimi()
      this.mvDetail()
    },
    mvDetail () {
      axios({
        url: 'https://autumnfish.cn/mv/detail',
        method: 'get',
        params: {
          mvid: this.mvids
        }
      }).then(res => {
        // console.log(res)
        if (res.data.code === 200) {
          this.mvDetailList = res.data.data
          if (this.mvDetailList.playCount > 100000) {
            this.mvDetailList.playCount = parseInt(this.mvDetailList.playCount / 10000) + '万'
          }
        }
      })
    }
  }
}
</script>

<style src='../assets/css/IN.detailedMV.css' scoped></style>
